import React from "react";
import { useState } from "react";

export default function Chatbot() {
  const [messages, setMessages] = useState([
    { sender: "user", text: "Hello, how are you?" },
    { sender: "bot", text: "I'm doing well, thank you! How can I help you today?" }
  ]);
  const [input, setInput] = useState("");

  const sendMessage = async () => {
    if (!input.trim()) return;

    const newMessages = [...messages, { sender: "user", text: input }];
    setMessages(newMessages);

    try {
      const response = await fetch("/calculate", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ expression: input })
      });

      const data = await response.json();
      setMessages([...newMessages, { sender: "bot", text: data.result || "Error processing request" }]);
    } catch (error) {
      setMessages([...newMessages, { sender: "bot", text: "API error!" }]);
    }

    setInput("");
  };

  return (
    <div className="w-80 h-[500px] bg-white rounded-lg shadow-lg flex flex-col">
      <div className="bg-purple-300 p-4 flex items-center justify-between rounded-t-lg">
        <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center text-black font-bold">
          BB
        </div>
        <button className="text-white text-xl">✕</button>
      </div>

      <div className="flex-1 p-4 overflow-y-auto space-y-4">
        {messages.map((msg, index) => (
          <div key={index} className={`flex ${msg.sender === "user" ? "justify-end" : "justify-start"}`}>
            <div className={`p-3 rounded-lg max-w-xs ${msg.sender === "user" ? "bg-purple-400 text-white" : "bg-blue-100 text-black"}`}>
              {msg.text}
            </div>
          </div>
        ))}
      </div>

      <div className="border-t p-3 flex items-center">
        <input
          type="text"
          placeholder="Reply..."
          className="flex-1 p-2 rounded-full border focus:outline-none"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyPress={(e) => e.key === "Enter" && sendMessage()}
        />
        <button className="ml-2 bg-purple-400 text-white p-2 rounded-full" onClick={sendMessage}>
          ➤
        </button>
      </div>
    </div>
  );
}

